<template>
    <transition name="modal">
        <div
            v-if="getModalShouldOpen"
            class="modal"
            :class="{
                'modal-side': componentConfig.type === 'side',
                'modal-center': componentConfig.type === 'center',
                'modal-from-left': componentConfig.transitionFrom === 'left',
                'modal-from-right': componentConfig.transitionFrom === 'right',
                'modal-from-top': componentConfig.transitionFrom === 'top'
            }">

            <div class="backdrop" @click="closeModal"></div>

            <div
                @close="closeModal"
                class="modal-container"
                :class="[
                    {'is-menu': componentConfig.isMobileMenu},
                    componentConfig.size
                ]">

                <span
                    @click="closeModal"
                    v-if="componentConfig.showCloseButton"
                    class="modal-close close-icon"
                    :class="componentConfig.type">
                </span>
                <component :is="componentConfig.component"
                           :extra-props="componentConfig.props"></component>
            </div>
        </div>
    </transition>
</template>
<script>
    import {mapGetters} from 'vuex';
    import ModalService from '@/services/modal';
    import Icon from '@/components/Globals/Icon';

    export default {
        name: 'UiModal',
        data() {
            return {
                componentConfig: {
                    component: undefined,
                    url: undefined,
                    type: 'side',
                    transitionFrom: 'left',
                    size: 'sm',
                    isMobileMenu: true,
                    showCloseButton: false
                }
            };
        },
        methods: {
            closeModal() {
                ModalService.closeModal();
            }
        },
        computed: {
            ...mapGetters(['getModalShouldOpen'])
        },
        watch: {
            'getModalShouldOpen': function (data) {
                if (data) {
                    this.componentConfig = ModalService.getModal();
                }
            }
        },
        components: {
            Icon
        },
        created() {
            document.addEventListener('keydown', event => {
                if (event.code === 'Escape' && this.getModalShouldOpen) {
                    this.closeModal();
                }
            });
        },
        destroyed() {
            document.removeEventListener('keydown', this.closeModal);
        }
    };
</script>
<style scoped lang="scss">
    @import "@/assets/scss/includes.scss";

    $modal-sm-width: 280px;
    $modal-md-width: 480px;
    $modal-lg-width: 600px;
    $modal-center-height: 100px;

    .modal {
        height: 100%;
        left: 0;
        position: fixed;
        top: 0;
        transition: transform 0.3s ease-out;
        z-index: 100;

        .sm {
            width: $modal-sm-width;
        }

        .md {
            width: $modal-md-width;
        }

        .lg {
            width: $modal-lg-width;
        }

        &.modal-side {

            .modal-container {
                background: $color-gray-1;
                border: none;
                box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
                height: 100vh;
                position: relative;
                transform: none;
                transition: transform 0.3s ease-out;
            }
        }

        &.modal-center {
            left: 50%;
            display: flex;
            align-items: center;
            right: auto;

            .modal-container {
                background: $color-gray-3;
                box-shadow: 0 5px 15px rgba(0, 0, 0, 0.5);
                min-height: $modal-center-height;
                position: relative;
                transform: none;
                padding: 1em;
                transition: transform 0.3s ease-out;
                width: 600px;

            }
        }

        &.modal-from-left {
            left: 0;
            right: auto;
        }

        &.modal-from-right {
            left: auto;
            right: 0;
        }

        &.modal-enter-active,
        &.modal-leave {
            transition: transform 0s ease-out;
        }
    }

    .modal-center {
        .modal-container {
            margin-top: -1 * $modal-center-height / 2;
        }

        .sm {
            margin-left: -1 * $modal-sm-width / 2;
        }

        .md {
            margin-left: -1 * $modal-md-width / 2;
        }

        .lg {
            margin-left: -1 * $modal-lg-width / 2;
        }
    }

    .modal-enter,
    .modal-enter-active,
    .modal-leave,
    .modal-leave-active {
        &.modal-from-left {
            .modal-container {
                transform: translateX(-102%);
            }
            .backdrop {
                opacity: 0;
            }
        }

        &.modal-from-right {
            .modal-container {
                transform: translateX(100%);
            }
            .backdrop {
                opacity: 1;
            }
        }

        &.modal-from-top {
            .modal-container {
                transform: translateY(-100%);
            }
            .backdrop {
                opacity: 1;
            }
        }
    }

    .backdrop {
        background-color: rgba(0, 0, 0, 0.8);
        bottom: 0;
        height: 100%;
        left: 0;
        position: fixed;
        top: 0;
        right: 0;
        width: 100%;
        z-index: 0;
        transition: opacity 0.3s ease-out;
    }

    .modal-close {
        cursor: pointer;
        display: block;
        font-size: 20px;
        left: -20px;
        line-height: 0;
        margin-right: -20px;
        position: absolute;
        text-align: center;
        top: 10px;
        z-index: 1;

        &.center {
            position: relative;
            width: 40px;
            left: calc(100% - 40px);
            margin-top: -10px;
            opacity: 0.7;

            &:hover {
                opacity: 1;
            }

            .close-icon {
                margin-left: 0;
            }
        }

        &:before {
            margin: 0;
        }

        &.close-icon {
            width: 40px;
